<template>
  <div>
    <el-card style="text-align: left;width: 990px;margin: 35px auto 0 auto">
      <div v-if="scenario">
        <el-button style="background-color:gainsboro;width:160px;float:right" @click="downloadJson">
          <span style="font-size: 26px;font-weight: bold">下载</span></el-button>
        <span style="font-size: 30px"><strong>{{scenario.name}}详细信息</strong></span>
        <el-divider></el-divider>
        <p><span style="font-size: 20px">{{scenario.intro}}</span></p>
        <div><span style="font-size: 20px">车道类型:{{scenario.drivewayType.type}}</span> </div>
        <div><span style="font-size: 20px">无人车规划任务:{{scenario.taskPlanning.task}}</span></div>
        <div><span style="font-size: 20px">障碍物类型:{{scenario.obstacleType.type}}</span></div>
        <div><span style="font-size: 20px">障碍物数量:{{scenario.num}}</span></div>
        <video controls width="900" :src="scenario.video" type="video/mp4"></video>
      </div>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: 'ScenarioIntro',
    data () {
      return {
        scenario: ''
      }
    },
    mounted () {
      this.loadScenario()
    },
    methods: {
      loadScenario () {
        var _this = this
        this.$axios.get('/scenarios/' + this.$route.query.id).then(resp => {
        if (resp && resp.data.code === 200) {
          _this.scenario = resp.data.result
          // console.log(_this.scenario)
          }
        })
      },
      downloadJson () {
        let _this = this
        this.$axios.get('/scenarios/download/' + this.scenario.id, {
          responseType: 'blob'
        }).then(resp => {
          const blob = new Blob([resp.data], {
            type: 'application/json'
          })
          const link = document.createElement('a')
          link.href = window.URL.createObjectURL(blob) // 指定下载链接
          link.setAttribute('download', _this.scenario.fileName) // 指定下载文件名
          document.body.appendChild(link)
          link.click()
          window.URL.revokeObjectURL(link.href) // 释放URL对象
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/markdown.css";
</style>



